import Link from 'next/link';
import Image from 'next/image';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { formatMileage, formatPrice } from '@/lib/utils';
import { getImageById } from '@/lib/placeholder-images';
import { Calendar, Gauge, Car } from 'lucide-react';
import type { Vehicle } from '@/lib/types';

type VehicleCardProps = {
  vehicle: Vehicle;
};

export default function VehicleCard({ vehicle }: VehicleCardProps) {
  const mainImage = getImageById(vehicle.images[0]);
  
  return (
    <Link href={`/vehicles/${vehicle.id}`} className="group block">
      <Card className="h-full overflow-hidden transition-all duration-300 ease-in-out hover:shadow-xl hover:-translate-y-1">
        <CardHeader className="p-0">
          <div className="relative h-56 w-full">
            <Image
              src={mainImage?.imageUrl || ''}
              alt={mainImage?.description || `Image of ${vehicle.make} ${vehicle.model}`}
              fill
              className="object-cover transition-transform duration-300 group-hover:scale-105"
              data-ai-hint={mainImage?.imageHint}
            />
             <Badge variant="secondary" className="absolute right-2 top-2">{vehicle.type}</Badge>
          </div>
        </CardHeader>
        <CardContent className="p-4">
          <CardTitle className="mb-2 text-lg font-bold group-hover:text-primary">
            {vehicle.make} {vehicle.model}
          </CardTitle>
          <p className="mb-4 text-2xl font-semibold text-primary">{formatPrice(vehicle.price)}</p>
          <div className="grid grid-cols-2 gap-2 text-sm text-muted-foreground">
            <div className="flex items-center gap-2">
              <Calendar className="h-4 w-4" />
              <span>{vehicle.year}</span>
            </div>
            <div className="flex items-center gap-2">
              <Gauge className="h-4 w-4" />
              <span>{formatMileage(vehicle.mileage)} mi</span>
            </div>
            <div className="flex items-center gap-2">
              <Car className="h-4 w-4" />
              <span>{vehicle.specifications.find(s => s.name === 'Transmission')?.value}</span>
            </div>
             <div className="flex items-center gap-2">
                <div className="h-4 w-4 flex items-center justify-center">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M14 6V4h-4v2"/><path d="M12 18a4.5 4.5 0 0 0 4.5-4.5V9.75a4.5 4.5 0 1 0-9 0V13.5A4.5 4.5 0 0 0 12 18Z"/></svg>
                </div>
                <span>{vehicle.specifications.find(s => s.name === 'Engine')?.value}</span>
            </div>
          </div>
        </CardContent>
      </Card>
    </Link>
  );
}
